import React from 'react';
import Atlassian from '../images/atlassian.svg';
import Microsoft from '../images/microsoft.svg';
import Github from '../images/github (1).svg';

function Partnership(){
    return(
        <div className='partnership'>
            <h2>Our Partnerships</h2>
            <div className='partner'>
                <div><img src={Atlassian} alt="atlassian_picture" /></div>
                <div><img src={Microsoft} alt="microsoft_picture" /></div>
                <div><img src={Github} alt="github_picture" /></div>
            </div>
            <div className='skillPartner'>
                <h3>Skilled in Technologies You Need</h3>
                <h4>Hire remote software developers with knowledge of the exact technology stack your product is built with</h4>
                <div className='skillTech'>
                    <div className='skilled'>
                        <div className='skills'>
                            <p>Back-End Engineer</p>
                            <p>QA Engineer</p>
                            <p>React Native Developer</p>
                            <p>Java Developer</p>
                        </div>
                        <div className='skills'>
                            <p>Front-End Engineer</p>
                            <p>React Developer</p>
                            <p>Python Developer</p>
                            <p>C# Developer</p>
                        </div>
                    </div>
                    <div className='skilled'>
                        <div className='skills'>
                            <p>Full-Stack Engineer</p>
                            <p>Android Developer</p>
                            <p>Node.JS Developer</p>
                            <p>Devops Engineer</p>
                        </div>
                        <div className='skills'>
                            <p>Mobile Engineer</p>
                            <p>iOS Developer</p>
                            <p>Javascript Developer</p>
                            <p>UI/UX Developer</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
};

export default Partnership;