import React from 'react';
import Decagon from '../images/logo-white.svg'
import Menu from '../images/menu.svg'

function Header(){

    return(
        <div className='header'>
            <img src={Decagon} alt="Decagon_logo" />
            <div>
                <img src={Menu} alt="menu_logo" />
                <p><a href='#'>Build Your Dev Team</a></p>
            </div>
        </div>
    )
}
export default Header;