import React from 'react';
import Merk from '../images/merck.svg';
import Nike from '../images/nike.svg';
import MasterCard from '../images/mastercard.svg';
import Vertex from '../images/vertex.svg';
import Budweiser from '../images/budweiser.svg';
import Nokia from '../images/nokia.svg';
import Workspace from '../images/workspace.jpg';
import Plug from '../images/plug.svg';
import Hand from '../images/hand.svg';


function Reason(){
    return(
        <div className='reason'>
            <div className='companyLogos'>
                <p>Companies that hire from Decagon</p>
                <div className='reasonImage'>
                    <img src={Merk} alt="Merk_logo" />
                    <img src={Nike} alt="Nike_logo" />
                    <img src={MasterCard} alt="MasterCard_logo" />
                    <img src={Vertex} alt="Vertex_logo" />
                    <img src={Budweiser} alt="Budweiser_logo" />
                    <img src={Nokia} alt="Nokia_logo" />
                </div>
            </div>
            <div className='WhyDec'>
                <h2>Why Decagon?</h2>
                <div className='myWhyDec'>
                    <img src={Workspace} alt="workspace_picture" />
                    <div className='myWorkspace'>
                        <div className='workspace'>
                            <h2>On-Demand Hiring</h2>
                            <p>We’ve done all the work to save you lengthy time-to-hire. Software developers in our talent pool are fully vetted, with a wide range of experience to meet all your hiring needs.</p>
                        </div>
                        <div className='workspace'>
                            <div>
                                <img src={Plug} alt="plug_picture" />
                                <h2>Easy to Work With</h2>
                            </div>
                            <p>Coding skills are good, but soft skills, and the right language are also essential. Hiring from Decagon gives you remote developers with leadership, communication, and collaborative capabilities - making them easy to work with.</p>
                        </div>
                        <div className='workspace'>
                            <div>
                                <img src={Hand} alt="plug_picture" />
                                <h2>Diverse, Gender Balance</h2>
                            </div>
                            <p>We believe in pursuing excellence through diversity. Hire developers from Africa, and other diverse backgrounds. Our goal is also to have 50% female software engineers in our pool by 2025</p>
                        </div>
                        <div className='workspace'>
                            <h2>Large, Quality Dev Pool</h2>
                            <p>Our promise is quality talent that has the perfect fit for your engineering team. Hire the top 0.5% of developers in Africa, 100% ready to help you scale quickly and build efficiently.</p>
                        </div>
                    </div>
                </div>
            </div>
            <div className='space'>
                <div className='innerSpace'></div>
            </div>
        </div>
    )
};

export default Reason;